.b-burger {
  display: none;
  border: none;
  background: transparent;
  box-shadow: none;
  width: 32px;
  height: 20px;
  position: relative;
  transform: rotate(0);
  transition: .5s ease-in-out;
  cursor: pointer;
}

.b-burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  border-radius: 9px;
  transform: rotate(0);
  transition: .25s ease-in-out;
  opacity: 1;
  left: 0;
}

.b-burger span:nth-child(1) {
  top: 0;
  transform-origin: center center;
}

.b-burger span:nth-child(2) {
  top: 50%;
  transform-origin: left center;
}

.b-burger span:nth-child(3) {
  top: 100%;
  transform-origin: center center;
}

.b-burger--active span:nth-child(1) {
  transform: translate(0, -50%) rotate(45deg);
  top: 50%;
}

.b-burger--active span:nth-child(2) {
  width: 0;
  opacity: 0;
}

.b-burger--active span:nth-child(3) {
  transform: translate(0, -50%) rotate(-45deg);
  top: 50%;
}

.b-button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 50px;
  width: 300px;
  height: 60px;
  transition: all .3s;
  border: none;
  box-shadow: none;
  background: white;
}

.b-button__content {
  color: #000;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .b-button__content {
    font-size: .7rem;
  }
}

.b-header {
  color: #fff;
  height: 95px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.b-header .container {
  padding: 28px 0;
  height: 100%;
  border-bottom: 1px solid #EFECEA;
}

.b-header__nav-list {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.b-header__nav-item:first-child {
  margin-right: 145px;
}

.b-header__nav-item:last-child {
  margin-left: 145px;
}

.b-header__nav-logo path {
  fill: #fff;
}

.b-header__nav-link {
  border-bottom: 1px solid transparent;
  transition: border-bottom 0.2s ease-out;
}

.b-header__nav-link:hover {
  border-bottom: 1px solid #fff;
}

.b-banner {
  height: 850px;
  background-image: url("../img/index-page/1-banner/bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

.b-banner__title {
  font-family: 'PlayfairDisplay Regular';
  font-size: 4.375rem;
  line-height: 3.66rem;
  letter-spacing: 3px;
  max-width: 928px;
  margin: 0 auto;
  padding-top: 24%;
  margin-bottom: 10%;
  text-align: center;
  font-weight: 300;
}

.b-banner__title span {
  display: block;
}

.b-banner__title span:first-child {
  margin-bottom: 30px;
}

.b-banner__title b, .b-banner__title a {
  font-family: 'PlayfairDisplay Bold';
  font-weight: 600;
}

.b-banner__title .und {
  display: inline-block;
  position: relative;
}

.b-banner__title .und:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -14%;
  width: 100%;
  height: 2px;
  background-color: #fff;
}

.b-banner__select-group {
  display: flex;
  width: 100%;
  justify-content: center;
}

.b-banner__select--name .select2-container {
  width: 256px !important;
}

.select2 .select2-container .select2-container--default {
  width: 159px !important;
}

.b-banner__select-group .select2-container--default .select2-selection--single {
  height: 48px;
  border-radius: 0;
}

.b-banner__select-group .select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 45px;
  font-family: 'PlayfairDisplay Regular', sans-serif;
  color: #6E6660;
  padding-left: 16px;
}

.b-banner__select-group .select2-container--default .select2-selection--single .select2-selection__arrow {
  height: 100%;
}

.select2-search .select2-search--dropdown {
  display: none !important;
}

.select2-search__field {
  display: none !important;
}

.select2-results__option {
  font-family: 'Lato Regular', sans-serif !important;
  padding: 12px 31px 12px 16px !important;
  color: #271317 !important;
  background-color: #fff;
  transition: background-color .3s ease-out;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: #EFECEA !important;
}

.select2-results__option {
  position: relative;
}

.select2-results__option:before, .select2-results__option:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 18px;
  width: 13px;
  height: 1px;
  background-color: #000;
}

.select2-results__option:after {
  transform: rotate(90deg);
}

.select2-selection__arrow {
  position: relative;
}

.select2-selection__arrow b {
  display: none;
}

.select2-selection__arrow:before, .select2-selection__arrow:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 18px;
  width: 13px;
  height: 1px;
  background-color: #6E6660;
}

.select2-selection__arrow:before {
  transform: rotate(-45deg);
  transition: all .08s ease-out;
}

.select2-selection__arrow:after {
  transform: rotate(45deg);
  right: 26px;
  transition: all .08s ease-out;
}

.reverse:before {
  transform: rotate(45deg);
}

.reverse:after {
  transform: rotate(-45deg);
  right: 26px;
}

.select2-container--default .select2-selection--single {
  border: none !important;
  border-right: 1px solid #C4C4C4 !important;
}

.b-banner__select:last-child + .select2-container .select2-container--default .select2-selection--single {
  border-right: none !important;
}

.select2-container--default .select2-selection--single[aria-expanded="true"] {
  border: 1px solid #C4C4C4 !important;
  border-left: none !important;
}

.select2-search--dropdown {
  padding: 0;
}

.b-banner__submit {
  font-family: 'PlayfairDisplay Bold', sans-serif;
  padding: 12px 16px;
  color: #fff;
  border: 1px solid #fff;
  margin-left: 32px;
}
